@charset "utf-8";

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
input,
textarea,
button,
footer,
header,
nav {
  margin: 0;
  padding: 0;
}

header,
footer,
nav,
main {
  display: block;
}

body,
input,
textarea,
select,
button {
  font-family: $font-default;
}

body {
  position: relative;
  background-color: #fff;
}

img {
  border: 0;
  vertical-align: top;
}

ul,
ol {
  list-style: none;
}

em,
address {
  font-style: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  border: 0;
  border-radius: 0;
  background: none;
  cursor: pointer;
}
