@charset 'utf-8';
@import '../../scss/utils';

.header {
  padding: 20px 10px;
  text-align: center;
  opacity: 0;
  transition: all 0.5s linear;

  &.animation {
    opacity: 1;
  }
}

.thumbnail {
  overflow: hidden;
  position: relative;
  width: 100px;
  height: 100px;
  margin: 20px auto 0;
  border-radius: 50%;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 50%;
    border: 1px solid rgba(#000, 0.3);
  }
}

.date {
  display: block;
  margin: 0 auto;
  padding-top: 15px;
}
